body{
  padding: 40px 7%;
}
.container{
  display: grid;
  grid-template-columns: 20% 80%;
}
.list{
  /* width: 20%; */
}
.item{
  cursor: pointer;
  border: 1px solid #ffffff;
  border-bottom-color: #f0f0f0;
  transition: all .5s;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -ms-transition: all .5s;
  -o-transition: all .5s;
}
.item:hover{
  border-color: aqua;
  border-bottom-color: aqua;
}
.title{
  text-align: center;
}